.email_gate_active .logo, .email_gate_active .main-navigation {
  display: none;
}

.email_gate_active #content {
  padding-top: 110px;
}

.email_gate {
  max-width: 1480px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.email_gate .inner {
  flex: 100%;
}

.email_gate .title h2 {
  font-family: var(--font-druk);
  font-weight: var(--font-druk-weight);
  font-style: var(--font-druk-style);
  text-transform: uppercase;
  font-size: 15.5vw;
  line-height: 1;
}

.email_gate small {
  max-width: 410px;
  margin-top: 8px;
  font-size: 12px;
  display: block;
}

.email_gate input[type="submit"] {
  min-width: 30px;
  margin-top: 16px;
  padding: 5px 16px 3px;
}

.email_gate input[type="email"] {
  max-width: 410px;
  padding-left: 4px;
  padding-right: 4px;
  line-height: 30px;
}

.email_gate .text_and_form, .email_gate .image {
  flex: 100%;
}

.email_gate .text_and_form {
  margin-bottom: 24px;
}

@media screen and (width >= 768px) {
  .email_gate .text_and_form {
    z-index: 2;
    position: relative;
  }

  .email_gate .text_and_form .inner {
    align-self: center;
  }

  .email_gate .text_and_form .title {
    width: 140%;
  }

  .email_gate .text_and_form .title h2 {
    font-size: 11vw;
  }

  .email_gate .text_and_form {
    flex-basis: calc(50% - 20px);
    padding-right: 20px;
  }

  .email_gate .image {
    z-index: 1;
    flex-basis: calc(50% - 20px);
    padding-left: 20px;
    position: relative;
  }
}

@media screen and (width >= 1440px) {
  .email_gate .text_and_form .title h2 {
    font-size: 180px;
  }
}
